*{  
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    -webkit-user-select: none;
    -webkit-user-drag: none;
    -webkit-app-region:none;
}
body{
    background-color: rgb(180, 250, 115);
    display: grid;
    place-content: center;
    height: 100vh;
    font-size: 1.5rem;
    font-family: 'Roboto', sans-serif;
}
.container{
    height: 80vh;
    width: 50vh;
    background-color: black;
    border-radius: 1rem;
    padding: 1rem;
    -webkit-box-shodow:7px 7px 5px 0px rgba(50,50,50,0.75) ;
    -moz-box-shadow:7px 7px 5px 0px rgba(50,50,50,0.75) ;
    box-shadow:7px 7px 5px 0px rgba(50,50,50,0.75) ;
}
.wrapper{
    display: grid;
    height: 100%;
    width: 100%;
    grid-template-columns: repeat(4,1fr);
    grid-template-rows: repeat(6,1fr);
    gap: 10px;
}
.screen{
 grid-column: 1/-1;
 color: white;
 text-align: right;
 padding: 0.5rem;
 font-size: 3rem;
 overflow: hidden;
}
.btn{
    display: grid;
    place-content: center;
    background-color: rgba(54,52,52);
    border-radius: 50%;
    color: white;
    cursor: pointer;
    font-weight: bold;
}
.btn:hover{
    background-color: gray;
}
.lightgray{
    background-color: gray;
   color:black;
}
.lightgray:hover{
    background-color: white;
}
.operator{
    background-color: rgb(180, 250, 115);
    color: black;
}
.operator:hover{
    background-color: rgb(205, 250, 164);
}
.zero{
    grid-column:1/span 2 ;
    border-radius: 25px;
    align-items: center;
    place-content: unser;
}